<template>
  <div class="container mt-3">
    <small class="text-muted">{{ $options.__file }}</small>
    <h1 class="text-primary font-weight-bold">
      <router-link to="/"><img src="~bootstrap-icons/icons/caret-left-fill.svg" alt="caret icon" width="30" height="30"></router-link>
      Live-streaming con {{ name }}
    </h1>
    <p>Invia questo link a {{ name }}: <a target="_blank" :href="'https://zuly.it/store/' + store_id + '/live/' + session">https://zuly.it/store/{{ store_id }}/live/{{ session }}</a></p>
    <div class="btn-group" role="group" aria-label="Invia live-streaming link">
      <a class="btn btn-secondary" href="#" role="button"><img src="~bootstrap-icons/icons/telephone-outbound.svg" alt="phone icon" width="30" height="30"> Invia con un SMS</a>
      <a class="btn btn-secondary" href="#" role="button"><img :src="publicPath + 'WhatsApp.svg'" alt="whatsapp icon" width="30" height="30"> Invia con WhatsApp</a>
      <a class="btn btn-secondary" href="#" role="button"><img src="~bootstrap-icons/icons/envelope.svg" alt="email icon" width="30" height="30"> Invia tramite E-mail</a>
    </div>
    <hr>
    <video id="localVideo" autoplay muted playsinline class="mx-auto"></video>
    <button type="button" class="btn btn-secondary btn-lg w-100 mt-3 mb-3">Termina il live-streaming</button>
    <button type="button" class="btn btn-secondary btn-lg w-100 mt-3 mb-3">Proposta di acquisto</button>
  </div>
</template>

<script>
import 'webrtc-adapter'
import io from 'socket.io-client'

var pcConfig = {
  'iceServers': [{
    'urls': 'stun:stun.l.google.com:19302'
  },
  {
    'urls': 'turn:zuly.it:3478',
    'credential': 'test1',
    'username': 'test1'
  }]
}

export default {
  data () {
    return {
      publicPath: process.env.BASE_URL,
      store_id: null,
      session: '',
      name: 'Paolo Greppi',
      isChannelReady: false,
      isInitiator: false,
      isStarted: false,
      localStream: null,
      pc: null,
      socket: null
    }
  },
  methods: {
    sendMessage: function (message) {
      console.log('Client sending message: ', message)
      this.socket.emit('message', message)
    },
    gotStream: function (stream) {
      console.log('Adding local stream.')
      this.localStream = stream
      var localVideo = document.querySelector('#localVideo')
      localVideo.srcObject = stream
      this.sendMessage('got user media')
      if (this.isInitiator) {
        this.maybeStart()
      }
    },
    maybeStart: function () {
      console.log('>>>>>>> maybeStart() ', this.isStarted, this.localStream, this.isChannelReady)
      if (!this.isStarted && typeof this.localStream !== 'undefined' && this.isChannelReady) {
        console.log('>>>>>> creating peer connection')
        this.createPeerConnection()
        this.pc.addStream(this.localStream)
        this.isStarted = true
        console.log('isInitiator', this.isInitiator)
        if (this.isInitiator) {
          this.doCall()
        }
      }
    },
    createPeerConnection: function () {
      try {
        this.pc = new RTCPeerConnection(pcConfig)
        this.pc.onicecandidate = this.handleIceCandidate
        this.pc.onremovestream = this.handleRemoteStreamRemoved
        console.log('Created RTCPeerConnnection')
      } catch (e) {
        console.log('Failed to create PeerConnection, exception: ' + e.message)
        alert('Cannot create RTCPeerConnection object.')
      }
    },
    handleIceCandidate: function (event) {
      console.log('icecandidate event: ', event)
      if (event.candidate) {
        this.sendMessage({
          type: 'candidate',
          label: event.candidate.sdpMLineIndex,
          id: event.candidate.sdpMid,
          candidate: event.candidate.candidate
        })
      } else {
        console.log('End of candidates.')
      }
    },
    handleCreateOfferError: function (event) {
      console.log('createOffer() error: ', event)
    },
    doCall: function () {
      console.log('Sending offer to peer')
      this.pc.createOffer(this.setLocalAndSendMessage, this.handleCreateOfferError)
    },
    setLocalAndSendMessage: function (sessionDescription) {
      this.pc.setLocalDescription(sessionDescription)
      console.log('setLocalAndSendMessage sending message', sessionDescription)
      this.sendMessage(sessionDescription)
    },
    handleRemoteStreamRemoved: function (event) {
      console.log('Remote stream removed. Event: ', event)
    },
    handleRemoteHangup: function () {
      console.log('Session terminated.')
      this.stop()
      this.isInitiator = false
    },
    stop: function () {
      this.isStarted = false
      this.pc.close()
      this.pc = null
    }
  },
  mounted: function () {
    this.store_id = this.$route.params.id
    this.session = this.$route.params.contact_id

    this.socket = io.connect()

    if (this.session !== '') {
      this.socket.emit('create or join', this.session)
      console.log('Attempted to create or join room: ', this.session)
    }

    this.socket.on('created', (room) => {
      console.log('Created room ' + room)
      this.isInitiator = true
    })

    this.socket.on('full', (room) => {
      console.log('Room ' + room + ' is full')
    })

    this.socket.on('join', (room) => {
      console.log('Another peer made a request to join room ' + room)
      console.log('This peer is the initiator of room ' + room + '!')
      this.isChannelReady = true
    })

    this.socket.on('joined', (room) => {
      console.log('joined: ' + room)
      this.isChannelReady = true
    })

    this.socket.on('log', (array) => {
      console.log.apply(console, array)
    })

    // This client receives a message
    this.socket.on('message', (message) => {
      console.log('Client received message:', message)
      if (message === 'got user media') {
        this.maybeStart()
      } else if (message.type === 'answer' && this.isStarted) {
        this.pc.setRemoteDescription(new RTCSessionDescription(message))
      } else if (message.type === 'candidate' && this.isStarted) {
        var candidate = new RTCIceCandidate({
          sdpMLineIndex: message.label,
          candidate: message.candidate
        })
        this.pc.addIceCandidate(candidate)
      } else if (message === 'bye' && this.isStarted) {
        this.handleRemoteHangup()
      }
    })

    window.navigator.mediaDevices.getUserMedia({
      audio: false,
      video: true
    })
      .then(this.gotStream)
      .catch(function (e) {
        alert('getUserMedia() error: ' + e.name)
      })

    window.onbeforeunload = () => {
      this.sendMessage('bye')
    }
  }
}

</script>